import React, {useState} from 'react';
import './Card.css';
import Answers from "../Answers/Answers";
import {TQuestion} from "../../types";

interface ICardProps {
  questions: TQuestion[],
}

const getQuestion = ({id}: {id: number}) => fetch(`api/question?questionId=${id}`, {
  headers: {
    'Content-Type': 'application/json',
  }
})
  .then((res) => res.json())
  .catch((err) => {
    console.log('Error: ', err);
  });

export const Card: React.FC<ICardProps> = ({questions}) => {
  const [question, setQuestion] = useState({});
  console.log(question);

  const handleClick = () => {
    getQuestion({id: 4})
      .then((result) => {
        setQuestion(result);
    });
  };

  return (
    <div className="card">
      <div className="card__number">1/8</div>
      <h3 className="card__title">
        {questions[0].title}
      </h3>
      <div className="card__answers">
        <Answers questions={questions} />
      </div>
      <button onClick={handleClick} className="card__button" type="button">Дальше</button>
    </div>
  );
};
